/* eslint-disable react/prop-types */
/* eslint-disable react/no-unescaped-entities */
import { useEffect, useState } from "react";
import { CheckCircle, OctagonAlert } from "lucide-react";

import PageTitle from "../Helpers/PageTitle";
import SEOHeader from "../Partials/Headers/HeaderOne/SEOHeader";
import Layout from "../Partials/Layout";

export default function ProcessusCommandeCredit() {
  const [typeCredit, setTypeCredit] = useState("direct"); // "direct" | "banque"

  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);

  return (
    <>
      <SEOHeader
        title="CCBM Shop | Processus Commande à Crédit"
        description="Parcours de commande à crédit chez CCBM Shop : Crédit direct ou Crédit financé par banque — sans étape d’adhésion."
        keywords="CCBM Shop, crédit direct, crédit bancaire, RH, validation, Wave, Orange Money"
      />
      <Layout childrenClasses="pt-0 pb-0">
        <div className="terms-condition-page w-full bg-white pb-[30px]">
          <div className="w-full mb-[30px]">
            <PageTitle
              breadcrumb={[
                { name: "Accueil", path: "/" },
                { name: "Processus de commande de crédit", path: "/processus-de-commande-de-credit" },
              ]}
              title="Commande à Crédit — Crédit Direct & Crédit Bancaire (sans adhésion)"
            />
          </div>

          <div className="w-full lg:flex lg:space-x-[30px]">
            <div className="flex-1">
              <div className="max-w-5xl mx-auto p-6 bg-white rounded-lg shadow-md">
                {/* Notice générale */}
                <div className="mb-6 p-4 bg-yellow-50 border border-yellow-200 rounded-md">
                  <p className="text-yellow-700">
                    <strong>Note importante :</strong> Ces processus s’adressent aux employés d’entreprises
                    partenaires de CCBM Shop. Vérifiez auprès de votre service RH que votre entreprise est éligible.
                  </p>
                </div>

                {/* Sélecteur du type de crédit */}
                <div className="mb-6">
                  <div className="inline-flex rounded-lg overflow-hidden border border-gray-200">
                    <button
                      type="button"
                      onClick={() => setTypeCredit("direct")}
                      className={`px-4 py-2 text-sm font-medium transition ${
                        typeCredit === "direct" ? "bg-bleu-logo text-white" : "bg-white text-gray-700 hover:bg-gray-50"
                      }`}
                    >
                      Crédit direct
                    </button>
                    <button
                      type="button"
                      onClick={() => setTypeCredit("banque")}
                      className={`px-4 py-2 text-sm font-medium border-l border-gray-200 transition ${
                        typeCredit === "banque" ? "bg-bleu-logo text-white" : "bg-white text-gray-700 hover:bg-gray-50"
                      }`}
                    >
                      Crédit financé par banque
                    </button>
                  </div>
                </div>

                {/* Bloc commun : création compte */}
                <div className="space-y-8">
                  <Step title="Création du compte">
                    <p className="text-gray-600">
                      Créez votre compte, puis suivez les étapes ci-dessous selon le type de crédit. Aucune
                      <strong> adhésion</strong> n’est requise.
                    </p>
                  </Step>
                </div>

                {/* Contenu selon le type */}
                {typeCredit === "direct" ? <CreditDirect /> : <CreditBanque />}

                {/* Rappels généraux */}
                <div className="mt-8 p-4 bg-blue-50 border border-blue-200 rounded-md flex items-start space-x-3">
                  <OctagonAlert className="w-6 h-6 text-bleu-logo flex-shrink-0 mt-1" />
                  <div>
                    <h4 className="font-semibold text-blue-800">Important :</h4>
                    <p className="text-blue-700">
                      Assurez-vous que vos informations (identifiant entreprise, email, téléphone) sont exactes. En cas
                      de non-paiement dans les trois (3) jours suivant l’échéance, votre service RH pourra être notifié,
                      avec possibilité de prélèvement sur salaire selon les conditions du programme.
                    </p>
                  </div>
                </div>

                {/* Après activation */}
                <div className="mt-8 border-t border-gray-200 pt-6">
                  <h3 className="text-xl font-semibold mb-4">Après l’activation</h3>
                  <ul className="space-y-2">
                    <li className="flex items-center space-x-2">
                      <CheckCircle className="w-5 h-5 text-green-500" />
                      <span className="text-gray-700">Vous pouvez commander à crédit selon le parcours choisi.</span>
                    </li>
                    <li className="flex items-center space-x-2">
                      <CheckCircle className="w-5 h-5 text-green-500" />
                      <span className="text-gray-700">
                        Chaque commande reste soumise aux validations prévues (RH pour le direct, interne+banque pour le
                        bancaire).
                      </span>
                    </li>
                    <li className="flex items-center space-x-2">
                      <CheckCircle className="w-5 h-5 text-green-500" />
                      <span className="text-gray-700">Moyens de paiement acceptés : Wave et Orange Money.</span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
}

/* =========================
   Crédit Direct (sans adhésion)
   ========================= */
function CreditDirect() {
  return (
    <div className="space-y-8 mt-8">
      <div className="p-3 inline-flex items-center gap-2 rounded-full bg-green-50 border border-green-200">
        <span className="text-xs font-semibold text-green-700 uppercase tracking-wide">Parcours sélectionné :</span>
        <span className="text-xs font-semibold text-green-800">Crédit direct</span>
      </div>

      <Step number={1} title="Obtenir l’identifiant de l’entreprise">
        <p className="text-gray-600">
          Demandez à votre service RH l’identifiant unique de l’entreprise. Il permet de lier votre compte et d’activer
          le crédit.
        </p>
      </Step>

      <Step number={2} title="Saisir l’identifiant dans votre profil">
        <p className="text-gray-600">Profil &gt; Informations personnelles &gt; Renseignez l’identifiant.</p>
      </Step>

      <Step number={3} title="Validation par votre RH">
        <p className="text-gray-600">
          Votre RH vérifie votre éligibilité. <span className="font-medium">Aucune adhésion n’est requise</span>, seule
          la validation RH déclenche l’activation.
        </p>
      </Step>

      <Step number={4} title="Activation & échelonnement standard">
        <p className="text-gray-600">
          Une fois validé, vous recevez une notification (email/SMS). L’échelonnement par défaut :
        </p>
        <ul className="mt-2 space-y-2">
          <li className="flex items-center space-x-2">
            <CheckCircle className="w-5 h-5 text-green-500" />
            <span className="text-gray-700">50% à la validation de la commande</span>
          </li>
          <li className="flex items-center space-x-2">
            <CheckCircle className="w-5 h-5 text-green-500" />
            <span className="text-gray-700">20% puis 15% puis 15% sur les trois mois suivants</span>
          </li>
          <li className="flex items-center space-x-2">
            <CheckCircle className="w-5 h-5 text-green-500" />
            <span className="text-gray-700">
              Retard &gt; 3 jours : notification RH et mesures prévues par le programme
            </span>
          </li>
        </ul>
      </Step>
    </div>
  );
}

/* =========================
   Crédit financé par banque (sans adhésion)
   ========================= */
function CreditBanque() {
  return (
    <div className="space-y-8 mt-8">
      <div className="p-3 inline-flex items-center gap-2 rounded-full bg-indigo-50 border border-indigo-200">
        <span className="text-xs font-semibold text-indigo-700 uppercase tracking-wide">Parcours sélectionné :</span>
        <span className="text-xs font-semibold text-indigo-800">Crédit financé par banque</span>
      </div>

      <Step number={1} title="Relier votre compte à l’entreprise">
        <p className="text-gray-600">
          Renseignez l’identifiant fourni par le RH dans “Profil &gt; Informations personnelles”. Pas d’adhésion à
          soumettre.
        </p>
      </Step>

      <Step number={2} title="Remplir le formulaire de financement">
        <p className="text-gray-600">
          Depuis le tableau de bord, choisissez “Crédit financé par banque” puis complétez le formulaire (revenus,
          identité, justificatifs, etc.). Les documents requis sont indiqués à l’écran.
        </p>
      </Step>

      <Step number={3} title="Validation interne (CCBM Shop)">
        <p className="text-gray-600">
          <strong>Étape 1 :</strong> notre équipe vérifie la complétude et l’éligibilité de votre dossier.
        </p>
      </Step>

      <Step number={4} title="Validation de la banque partenaire">
        <p className="text-gray-600">
          <strong>Étape 2 :</strong> après validation interne, le dossier est transmis à la banque. L’activation n’est
          possible qu’après <strong>accord de la banque</strong> (taux, durée, frais communiqués avant confirmation).
        </p>
      </Step>

      <Step number={5} title="Activation & paiements">
        <p className="text-gray-600">
          Une fois l’accord reçu, vous êtes notifié(e). Les échéances et le coût suivent l’offre de la banque. En cas de
          non-paiement selon l’échéancier bancaire, les dispositions de la banque et du programme s’appliquent.
        </p>
      </Step>
    </div>
  );
}

/* ====== Composant Step réutilisable ====== */
const Step = ({ number, title, children }) => (
  <div className="flex items-start space-x-4 mb-6">
    {number ? (
      <div className="flex-shrink-0 w-8 h-8 bg-bleu-logo text-white rounded-full flex items-center justify-center font-bold">
        {number}
      </div>
    ) : null}
    <div className="flex-grow">
      <h3 className="text-lg font-semibold mb-2">{title}</h3>
      {children}
    </div>
  </div>
);
